.button1 {
  color: white;
  background-color: black;
  border: 1px solid black;
  border-radius: 4px;
  padding: 0 15px;
  cursor: pointer;
  height:80px;
  transition: all 0.2s ease-in-out;
  opacity: 0.5;
}
.button2:hover {
  -webkit-animation: spin 2s ease-in-out .5s infinite alternate;
          animation: spin 2s ease-in-out .5s infinite alternate;
}

@-webkit-keyframes spin {
  from{
    transform: rotateZ(0deg);
  }
  to{
    transform: rotateZ(360deg);
  }
}

@keyframes spin {
  from{
    transform: rotateZ(0deg);
  }
  to{
    transform: rotateZ(360deg);
  }
}

.fromRight {
  display: inline-block;
  margin: 0 0.5rem;
  -webkit-animation: bounceInRight;
          animation: bounceInRight;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

/* The line */
.vertical-timeline::before {
  background: #000000 !important;
  opacity: 0.9;
  border-radius: 30%;
}

.vertical-timeline-element-content {
  position: relative;
  margin-left: 60px;
  background: white;
  border-radius: 0.25em;
  padding: 1em;
  box-shadow: 0 0px 0 #000 !important;
}

.vertical-timeline-element-icon {
  box-shadow: 0 0 0 4px black, inset 0 2px 0 rgba(0, 0, 0, 0.08), 0 3px 0 4px rgba(0, 0, 0, 0.05) !important;
}

.vertical-timeline-element-content {
  position: relative;
  margin-left: 60px;
  background: white;
  border-radius: 0em !important;
  padding: 1em;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22) !important;

}



